'use strict';

// MODULES //

import iterationOrder from './../../../base/iteration-order';
import ndarray2object from './../../../base/ndarraylike2object';
import numel from './../../../base/numel';
import blockedaccessorForEach2d from './2d_blocked_accessors.js';
import blockedaccessorForEach3d from './3d_blocked_accessors.js';
import blockedaccessorForEach4d from './4d_blocked_accessors.js';
import blockedaccessorForEach5d from './5d_blocked_accessors.js';
import blockedaccessorForEach6d from './6d_blocked_accessors.js';
import blockedaccessorForEach7d from './7d_blocked_accessors.js';
import blockedaccessorForEach8d from './8d_blocked_accessors.js';
import blockedaccessorForEach9d from './9d_blocked_accessors.js';
import blockedaccessorForEach10d from './10d_blocked_accessors.js';
import blockedForEach2d from './2d_blocked.js';
import blockedForEach3d from './3d_blocked.js';
import blockedForEach4d from './4d_blocked.js';
import blockedForEach5d from './5d_blocked.js';
import blockedForEach6d from './6d_blocked.js';
import blockedForEach7d from './7d_blocked.js';
import blockedForEach8d from './8d_blocked.js';
import blockedForEach9d from './9d_blocked.js';
import blockedForEach10d from './10d_blocked.js';
import accessorForEach0d from './0d_accessors.js';
import accessorForEach1d from './1d_accessors.js';
import accessorForEach2d from './2d_accessors.js';
import accessorForEach3d from './3d_accessors.js';
import accessorForEach4d from './4d_accessors.js';
import accessorForEach5d from './5d_accessors.js';
import accessorForEach6d from './6d_accessors.js';
import accessorForEach7d from './7d_accessors.js';
import accessorForEach8d from './8d_accessors.js';
import accessorForEach9d from './9d_accessors.js';
import accessorForEach10d from './10d_accessors.js';
import accessorForEachnd from './nd_accessors.js';
import forEach0d from './0d.js';
import forEach1d from './1d.js';
import forEach2d from './2d.js';
import forEach3d from './3d.js';
import forEach4d from './4d.js';
import forEach5d from './5d.js';
import forEach6d from './6d.js';
import forEach7d from './7d.js';
import forEach8d from './8d.js';
import forEach9d from './9d.js';
import forEach10d from './10d.js';
import forEachnd from './nd.js';


// VARIABLES //

var FOR_EACH = [
	forEach0d,
	forEach1d,
	forEach2d,
	forEach3d,
	forEach4d,
	forEach5d,
	forEach6d,
	forEach7d,
	forEach8d,
	forEach9d,
	forEach10d
];
var ACCESSOR_FOR_EACH = [
	accessorForEach0d,
	accessorForEach1d,
	accessorForEach2d,
	accessorForEach3d,
	accessorForEach4d,
	accessorForEach5d,
	accessorForEach6d,
	accessorForEach7d,
	accessorForEach8d,
	accessorForEach9d,
	accessorForEach10d
];
var BLOCKED_FOR_EACH = [
	blockedForEach2d, // 0
	blockedForEach3d,
	blockedForEach4d,
	blockedForEach5d,
	blockedForEach6d,
	blockedForEach7d,
	blockedForEach8d,
	blockedForEach9d,
	blockedForEach10d // 8
];
var BLOCKED_ACCESSOR_FOR_EACH = [
	blockedaccessorForEach2d, // 0
	blockedaccessorForEach3d,
	blockedaccessorForEach4d,
	blockedaccessorForEach5d,
	blockedaccessorForEach6d,
	blockedaccessorForEach7d,
	blockedaccessorForEach8d,
	blockedaccessorForEach9d,
	blockedaccessorForEach10d // 8
];
var MAX_DIMS = FOR_EACH.length - 1;


// MAIN //

/**
* Invokes a callback function once for each ndarray element.
*
* ## Notes
*
* -   A provided ndarray should be an `object` with the following properties:
*
*     -   **dtype**: data type.
*     -   **data**: data buffer.
*     -   **shape**: dimensions.
*     -   **strides**: stride lengths.
*     -   **offset**: index offset.
*     -   **order**: specifies whether an ndarray is row-major (C-style) or column major (Fortran-style).
*
* @param {ArrayLikeObject<Object>} arrays - array-like object containing one input array
* @param {Callback} fcn - callback function
* @param {*} [thisArg] - callback function execution context
* @returns {void}
*
* @example
* import Float64Array from '@stdlib/array/float64';
* import naryFunction from '@stdlib/utils/nary-function';
* import log from '@stdlib/console/log';
*
* // Create a data buffer:
* var xbuf = new Float64Array( 12 );
*
* // Define the shape of the array:
* var shape = [ 3, 1, 2 ];
*
* // Define the array strides:
* var sx = [ 4, 4, 1 ];
*
* // Define the index offset:
* var ox = 1;
*
* // Create an ndarray-like object:
* var x = {
*     'ref': null,
*     'dtype': 'float64',
*     'data': xbuf,
*     'shape': shape,
*     'strides': sx,
*     'offset': ox,
*     'order': 'row-major'
* };
*
* // Apply the callback function:
* forEach( [ x ], naryFunction( log, 1 ) );
*/
function forEach( arrays, fcn, thisArg ) {
	var ndims;
	var shx;
	var x;

	// Unpack the ndarray and standardize ndarray meta data:
	x = ndarray2object( arrays[ 0 ] );
	shx = x.shape;
	ndims = shx.length;

	// Determine whether we can avoid iteration altogether...
	if ( ndims === 0 ) {
		if ( x.accessorProtocol ) {
			return ACCESSOR_FOR_EACH[ ndims ]( x, fcn, thisArg );
		}
		return FOR_EACH[ ndims ]( x, fcn, thisArg );
	}
	// Check whether we were provided an empty ndarray...
	if ( numel( shx ) === 0 ) {
		return;
	}
	// Determine whether we can avoid blocked iteration...
	if ( ndims <= MAX_DIMS && iterationOrder( x.strides ) !== 0 ) {
		// So long as iteration always moves in the same direction (i.e., no mixed sign strides), we can leverage cache-optimal (i.e., normal) nested loops without resorting to blocked iteration...
		if ( x.accessorProtocol ) {
			return ACCESSOR_FOR_EACH[ ndims ]( x, fcn, thisArg );
		}
		return FOR_EACH[ ndims ]( x, fcn, thisArg );
	}
	// Determine whether we can perform blocked iteration...
	if ( ndims <= MAX_DIMS ) {
		if ( x.accessorProtocol ) {
			return BLOCKED_ACCESSOR_FOR_EACH[ ndims-2 ]( x, fcn, thisArg );
		}
		return BLOCKED_FOR_EACH[ ndims-2 ]( x, fcn, thisArg );
	}
	// Fall-through to linear view iteration without regard for how data is stored in memory (i.e., take the slow path)...
	if ( x.accessorProtocol ) {
		return accessorForEachnd( x, fcn, thisArg );
	}
	forEachnd( x, fcn, thisArg );
}


// EXPORTS //

export default forEach;
